.title-accueil{font-family:"Montserrat",sans-serif;color:rgba(106,103,103,.5019607843);margin-top:0px;padding-top:300px;font-size:100px;text-align:center}@media screen and (max-width: 1024px){.title-accueil{font-size:80px;padding-top:300px}}@media screen and (max-width: 425px){.title-accueil{font-size:40px}}.title-présentation{font-family:"Montserrat",sans-serif;color:rgba(106,103,103,.5019607843);margin-top:0px;padding-top:10px;font-size:50px;text-align:center}@media screen and (max-width: 1024px){.title-présentation{font-size:40px}}@media screen and (max-width: 425px){.title-présentation{font-size:20px}}@keyframes slidein{from{transform:scaleX(0)}to{transform:scaleX(1)}}.accueil{background:linear-gradient(-45deg, #d1a6a7, #efe7df, #be8070, #6a6767);background-size:400% 400%;animation:gradient 15s ease infinite;height:100vh}@keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}